.header_outer_container {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  height: 10vh;

  transition: all 0.5s;
  transition-timing-function: ease-in;
  z-index: 1;
  background-color: rgb(0, 0, 0);
  color: white;
}
.header_container {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
}
.header_container ul li {
  margin: auto 10px;
}

.header_left {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header_right {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
